import axios from '@/libs/api.request';
/**
 * 添加
 */

var obj = new Object();

obj.addSnjbTmpl = function (data, fnOk, fnError) {
  return axios.request({
    url: 'snJbjPzjlTemplate/addTemplate',
    method: 'post',
    data: data
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};
/**
 * 查询
 */


obj.getSnjbTmplList = function (data, fnOk, fnError) {
  return axios.request({
    url: "snJbjPzjlTemplate/list?pageIndex=".concat(data.pageIndex, "&pageSize=").concat(data.pageSize),
    method: 'post'
  }).then(function (res) {
    console.log(res);

    if (res.data.code != "Success") {
      fnError(res.data);
    } else {
      fnOk(res.data);
    }
  });
};

